.popup {
  display: flex;
  visibility: hidden;
  opacity: 0;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  background: rgba(0, 0, 0, .5);
  transition: opacity .3s, visibility .3s;
}