.profile__about {
    margin: 0;
    font-size: 18px;
    font-weight: 400;
    line-height: 22px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 520px) {
    .profile__about {
        font-size: 14px;
        line-height: 1.1;
        max-width: 88%;
    }
}