.profile__name {
    margin: 0;
    max-width: 88%;
    font-size: 42px;
    font-weight: 500;
    line-height: 1.14;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 520px) {
    .profile__name {
        font-size: 27px;
    }
}