.form__field {
    width: 100%;
    height: 30px;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    box-sizing: border-box;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #000000;
    padding: 13px 0;
    margin: 30px 0 0;
  }

  .form__field:focus {
    outline: none;
    border-bottom: 2px solid rgba(0, 0, 0, .5);
  }